							<p>	Having been captivated by urology since year 3 of medical school, he has since authored several publications and presented at both national and international levels. Currently, Alex aims to pursue further academic training with hopes of becoming an academic urologist. His research interests include uro-oncology and technology.In his spare time, Alex is a keen landscape and travel photographer, and extremely occasional runner.</p>
